import React from 'react';
//Bootstrap and jQuery libraries
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import '../css/App.css'
import { withRouter } from 'react-router'

import LoaderComponent from './LoaderComponent';

class JobRuns extends React.Component {
  constructor(props){
    super(props); 
    this.job_run_id = ''
    this.filterOperation = ["IN","NOT IN"]
    this.state  = { 
      jobruns: [],
      filter: '',
      job_name: '',
      filter_start_date: '',
      filter_end_date: '',
      paginationValue: 0,
      loader:false,
      errors: [],
      is_error: false,
      notification: false,
      run_job_props: '',
    }

  }

  async fetchEtlFlowJobs() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    const limit = 10;
    const offset = this.state.paginationValue;

    var query = ` query { jobruns(limit: ${limit} , offset: ${offset}){ job_run_id job_name properties state start_time elapsed_time job_type is_master}}`
    
    if(this.props.match.params.job_name !== undefined){
      query = ` query { jobruns(limit: ${limit} , offset: ${offset} ,filter: "IN", jobName: \"${this.props.match.params.job_name}\"  )
              { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`

      if (this.state.filter_end_date !== "") {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\",filter: "IN", jobName: \"${this.props.match.params.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } 

      this.setState(prevState => ({
        filter: 'IN',
        job_name: this.props.match.params.job_name
      }))

    } else {
      if (this.state.filter_end_date !== "" && this.state.filter !== "" ) {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\",filter: \"${this.state.filter}\", jobName: \"${this.state.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else if (this.state.filter_end_date !== "" ){
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\")
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else if (this.state.filter !== "" && this.state.job_name !== "") {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset}, filter: \"${this.state.filter}\", jobName: \"${this.state.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else if (this.state.filter === "" && this.state.job_name !== "" ) {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset} ,filter: "IN", jobName: \"${this.state.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset}){ job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      }
    }

    this.setState(prevState => ({
      loader: true
    }))

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 

    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;   
  }

  async run_etl_job(job_name,props) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    var query =  `
      mutation {
        run_job (name:\"${job_name}\"){
          name
          props {key value}
      }
      }
    ` 
    if(props.length > 0) {
      var props_updated = [JSON.stringify(props).replaceAll("\"$","").replaceAll("$\"","")]   
      query =  `
      mutation {
        run_job (name:\"${job_name}\" ,props: ${props_updated}){
          name
          props {key value}
      }
      }
    ` 
    }
    this.setState(prevState => ({
      loader: true
    }))

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      )  
    })
    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    this.setState(prevState => ({
      loader: false
    }))
    const {data,errors} = await response.json();
    if(JSON.parse(JSON.stringify(data.run_job)) !== null){
      this.set_job_error_deails(false)
    }
    else {
      this.set_job_error_deails(true,JSON.parse(JSON.stringify(errors)))
    }
    return data;    
  }

  set_job_error_deails(is_error,errors){
    this.setState(prevState => ({
      is_error: is_error,
      errors: errors                    
      }))
  }

  setInterval() {
    this.timerID = setInterval(
      () => this.fetchEtlFlowJobs(),
      1000 * 1000
    );
  }
 
  componentDidMount() {
    this.set_jobruns()
    this.setInterval();
 }

 componentWillUnmount() {
  clearInterval(this.timerID);
 }

 set_jobruns(){
  this.fetchEtlFlowJobs().then(({jobruns}) => {
    this.setState(prevState => ({
      jobruns: jobruns
    }))
  })
 }

 set_job_name() {
  this.setState(prevState => ({
    job_name: document.getElementById("exampleDataList").value
  }))
 }

 set_filter_start_date() {
  this.setState(prevState => ({
    filter_start_date: document.getElementById("start_date").value
  }))
 }

 set_filter_end_date() {
  this.setState(prevState => ({
    filter_end_date: document.getElementById("end_date").value
  }))
 }

 show_modal(data)
 {
   var props = ''
   var data_jobruns = JSON.parse(data)

   for(var key in data_jobruns) {
   { 
       props  += "<b>"+ key + "</b>" +"=" + data_jobruns[key]+ "<br></br>"
   }
  {$("#jobRunModal .modal-body").html(props)}
  $("#jobRunModal").modal();
 }
}

retry_job_run(job_name,data)
 {
   var final_props = []
   var data_jobruns = JSON.parse(data)
   var default_props=["job_send_slack_notification", "job_enable_db_logging", "job_notification_level"];

   var i = 0
   var final_props = []
   var i;
   for(var key in data_jobruns) {
     if(!default_props.includes(key)){
        final_props[i] =  {["$key$"] : key,["$value$"] : data_jobruns[key]}
        i++
     }
   }
   var props = ''


   this.run_etl_job(job_name,final_props).then(({run_job}) => {
    this.set_notification(true)
    var data =  JSON.parse(JSON.stringify(run_job))
    if(data !== null){
        props = JSON.stringify(data["props"]) 
        var job_name = JSON.stringify(data["name"]) 
        this.set_job_deails(job_name,props)
   }});     

}

set_job_deails(job_name,props){
  this.setState(prevState => ({
    job_name: job_name,
    run_job_props: props                    
    }))
}

set_notification(state){
  this.setState(prevState => ({
    notification: state
  }))
}


async set_initial_state(){
  this.props.match.params.job_name = undefined
  this.setState(prevState => ({  
    filter: '',
    job_name: '',
    filter_start_date: '',
    filter_end_date: '',
    paginationValue: 0,
  }), () => this.set_jobruns())
}

show_job_run_state_modal(data)
 {
  {$("#jobRunStateModal .modal-body").html(data)}
  $("#jobRunStateModal").modal();
 }

 handleOnClick(job_id){    
  this.props.history.push('/JobRunDetails/' + job_id)
 }

 render(){
   
   var button = ''
   var filter_button_name = '' 
   var filter_button_job_name = '' 
   var filter_start_date = ''  
   var filter_end_date = ''  
   var filter_back_button = ''

   var notification_var = ""
    if(this.state.notification){
      if(this.state.is_error) {
         notification_var =
         <div class="alert alert-danger alert-dismissible fade show" role="alert">
            <h4 class="alert-heading">Error while submitting job!</h4>
            <p>{JSON.parse(JSON.stringify(this.state.errors)).map( (data) => data.message)}</p>
            <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
               <span aria-hidden="true">&times;</span>
            </button>
         </div>
       } else {
          notification_var =
          <div class="alert alert-success alert-dismissible fade show" role="alert">
             <h4 class="alert-heading">Job {this.state.job_name.replace(/^"(.*)"$/, '$1')} submitted successfully!  </h4>
             <p>{this.state.run_job_props}</p>
             <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
                <span aria-hidden="true">&times;</span>
             </button>
          </div>
        }  
    }

   //Code for adding pagination element
  if (this.state.paginationValue === 0) {
    button = <li class="page-item disabled">
    <input type="button" value="Previous" class="page-link btn btn-secondary btn-sm"/></li>
  } else {
    button =  <li class="page-item">
    <input type="button" value="Previous" class="page-link btn btn-secondary btn-sm" onClick = {() => {
             this.setState(prevState => ({
              paginationValue: this.state.paginationValue  -   10
            }))
            this.fetchEtlFlowJobs(this.state.paginationValue - 10).then(({jobruns}) => {
              this.setState(prevState => ({
                jobruns: jobruns
              }))
            }) 
    }}/></li>
  } 

   //Code for adding button name
  if (this.state.filter !== '') {
    filter_button_name = this.state.filter
  }else {
    filter_button_name = "Add Filter"
  } 

   //Code for adding button name
  if (this.state.job_name !== '') {
     filter_button_job_name = this.state.job_name
  }else {
     filter_button_job_name = "Type to search"
  } 

  if (this.state.filter_start_date !== '') {
   filter_start_date = this.state.filter_start_date
  }else {
    filter_start_date = "Start Date"
  } 

  if (this.state.filter_end_date !== '') {
    filter_end_date = this.state.filter_end_date
  }else {
    filter_end_date = "End Date"
  } 

  if(this.props.match.params.job_name !== undefined){
    filter_back_button= <button class= "btn btn btn-sm" type="button " onClick = {() => {
                          this.props.history.push('/')
                         }} >
                        Back
                        </button>
  }

   if (this.state.loader){
    return (
      <div>
        <LoaderComponent />
      </div>
    );
   }
   else{
   return (
     <div className="container-fluid">
       <h3>{this.state.loader}</h3>
       <div>{notification_var}</div>
       <ul class = "pagination justify-content-center">
         <div class="dropdown">
            <button class="btn btn-light dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
               {filter_button_name}
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              { this.filterOperation.map ( filter => (
                   <a class="dropdown-item">
                      <button class= "btn btn btn-sm" type="button " onClick = {() => {
                           this.setState(prevState => ({
                               filter: filter
                       }))}}> {filter} </button>
                   </a>
              ))}
            </div>
         </div>
         <div className ="form-inline float-right">
             <div className="mb-1">
             <input type="text"
                        className="form-control input-sm"
                        list = "datalistOptions"
                        id = "exampleDataList"
                        placeholder = {filter_button_job_name}
                        onChange={e => this.set_job_name()}></input>      
             </div>
             <datalist id = "datalistOptions">
               {this.props.jobs.map( data => (
                 <option value = {data.name}></option>
               ))}
             </datalist> 
         </div>
         <div class = "form-inline float-right">
              <div class = "mb-1"> 
                  <input type="date"
                         className="form-control input-sm"
                         placeholder={filter_start_date}
                         id = "start_date"
                         onChange={e => this.set_filter_start_date()}
                   ></input>
              </div>
              <div class = "mb-1"> 
                  <input type="date"
                         className="form-control input-sm"
                         placeholder={filter_end_date}
                         id = "end_date"
                         onChange={e => this.set_filter_end_date()}
                   ></input> 
              </div>    
         </div> 
         <button class= "btn btn btn-sm" type="button " onClick= {() => {
            this.set_initial_state()
            }}>
            Clear
         </button>
         <button class= "btn btn btn-sm" type="button " onClick = {() => {
                this.set_jobruns()
             }} >
            Refresh
         </button>
         {filter_back_button}
       </ul>
       <table id="jobruns" class="table table-sm table-bordered">
           <thead class="thead-dark">
                 <tr>
                   <th>Job Name</th>
                   <th>Job Type</th>
                   <th>Job Start Time</th>
                   <th>Elapsed Time</th>
                   <th>Job Status</th>
                   <th>Actions</th>
                 </tr>
               </thead>
                 <tbody>
                 {this.state  .jobruns.map( data => (
                   <tr>
                     <td> <button type="button" class="btn btn-link" data-dismiss="modal" onClick = {() => this.handleOnClick(data.job_run_id)}>{data.job_name}</button></td>
                     <td>{data.job_type}</td>
                     <td>{data.start_time}</td>
                     <td>{data.elapsed_time}</td>
                     <td>
                     {(() => {
                           if (data.state === "pass" || data.state == "" ) {
                             return (
                               <div>
                                 <p className = "text-success">{data.state}</p>
                               </div>
                             )
                           } else if (data.state === "started" || data.state == "running" ) {
                             return (
                               <div>
                                 <p className = "text-warning">{data.state}</p>
                               </div>
                             )
                           } else {
                            return (
                              <div>
                                 <button type="button" class="btn btn-danger btn btn-sm" data-toggle="modal" onClick = {() => this.show_job_run_state_modal(data.state)}>
                                     Error
                                 </button>
                            
                                 <div class="modal fade" id="jobRunStateModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                   <div class="modal-dialog" role="document">
                                     <div class="modal-content">
                                       <div class="modal-header">
                                         <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                         <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                           <span aria-hidden="true">&times;</span>
                                         </button>
                                       </div>
                                       <div class="modal-body">
                                       </div>
                                       <div class="modal-footer">
                                         <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                       </div>
                                     </div>
                                   </div>
                                 </div>
                              </div>
                            )
                           }
                         })()}
                     </td>
                     <td>
                     <div class='row'>
                     <div class='col-xs-12 button-wrapper' class="inner">
                         <button type="button" class="btn btn-secondary btn-sm ml-2" data-toggle="modal" onClick = {() => this.show_modal(data.properties)}>
                           Properties
                         </button>
                         
                         <div class="modal fade" id="jobRunModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                           <div class="modal-dialog" role="document">
                             <div class="modal-content">
                               <div class="modal-header">
                                 <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                 <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                   <span aria-hidden="true">&times;</span>
                                 </button>
                               </div>
                               <div class="modal-body">
                               </div>
                               <div class="modal-footer">
                                 <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                               </div>
                             </div>
                           </div>
                         </div>

                         {(() => {
                           if (data.state === "pass" || data.state == "" || data.state == "started" ) {
                             return (
                               <div>
                               </div>
                             )
                           } else {
                             return (
                               <div class="inner">  
                                  <button type="button" class="btn btn-warning btn-sm ml-2" data-toggle="modal" onClick = {() => this.retry_job_run(data.job_name,data.properties)}>
                                      Retry
                                  </button>
                               </div>
                             )
                           } 
                         })()}
                        </div>
                        </div> 
                     </td>
                   </tr>
                 ))}
                 </tbody>
            </table>  
            <nav aria-label="Page navigation example">
               <ul class="pagination justify-content-center">
                 {button}
                 <li class="page-item"><input type="button" value="Next" class="page-link btn btn-secondary btn-sm" onClick = {() => {
                   this.setState(prevState => ({
                     paginationValue: this.state.paginationValue  + 10
                   }))
                   this.fetchEtlFlowJobs(this.state.paginationValue + 10).then(({jobruns}) => {
                    this.setState(prevState => ({  
                      jobruns: jobruns
                    }))
                   })
                   }}/></li>
               </ul>
         </nav>
          </div>
   );
  }
 }
}
// export default JobRuns;     
export default withRouter(JobRuns)
