import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

import '../css/App.css'
import { withRouter } from 'react-router'

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';

class JobList extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { 
      jobs: [],
      notification: false,
      run_job_props: '',
      job_name: '',
      errors: [],
      is_error: false,
      is_active: true,
      values: [],keys: []
    }
  }

  async updateJobActiveState(job_name,state) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
            mutation {
              update_job_state (name:\"${job_name}\" ,state: ${state}){
             }
           }
          `
        }
      )  
    })
    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    const {data} = await response.json();
    return data;    
  }

  async run_etl_job(job_name,props) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    var query =  `
      mutation {
        run_job (name:\"${job_name}\"){
          name
          props {key value}
      }
      }
    ` 
    if(props.length > 0) {
      var props_updated = JSON.stringify(props).replaceAll("\"$","").replaceAll("$\"","")
      query =  `
      mutation {
        run_job (name:\"${job_name}\" ,props: ${props_updated}){
          name
          props {key value}
      }
      }
    ` 
    }
    this.setState(prevState => ({
      loader: true
    }))
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      )  
    })
    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    this.setState(prevState => ({
      loader: true
    }))
    const {data,errors} = await response.json();
    if(JSON.parse(JSON.stringify(data.run_job)) !== null){
      this.set_job_error_deails(false)
    }
    else {
      this.set_job_error_deails(true,JSON.parse(JSON.stringify(errors)))
    }
    return data;    
  }


 componentDidMount() {
    var dt = $('#joblist').DataTable({
      "order": [[ 6, "desc" ]]
  });
    dt.column(6).visible(false);
    dt.column(3).visible(false);

  }

 show_modal(data)
  {
    var job_properties = "" 
    { 
      data.map(  props =>
        job_properties  += "<b>"+ props.key + "</b>" +"=" + props.value + "<br></br>"
    )}

   {$("#jobListeModal .modal-body").html(job_properties)}
   $("#jobListeModal").modal();
  }

  set_notification(state){
    this.setState(prevState => ({
      notification: state
    }))
  }

  set_job_deails(job_name,props){
    this.setState(prevState => ({
      job_name: job_name,
      run_job_props: props                    
      }))
  }

  set_job_error_deails(is_error,errors){
    this.setState(prevState => ({
      is_error: is_error,
      errors: errors                    
      }))
  }

  handleOnClick(job_name){    
    this.props.history.push('/JobRuns/' + job_name)
  }

  create_ui(){
    return this.state.values.map((el, i) => 
        <div key={i}>
        <div class="row">
          <div class="col-xs-4 col-sm-4 col-md-4">
            <div class="form-group">
               <input class="form-control" value={this.state.keys[i]} type="text" placeholder="Type Key" onChange={this.set_run_job_keys.bind(this, i)} />
            </div>
          </div>  
          <div class="col-xs-7 col-sm-7 col-md-7">
            <div class="form-group">
              <input class="form-control" value={el||''} type="text"  placeholder="Type Value" onChange={this.set_run_job_values.bind(this, i)} />
            </div>
          </div> 
         <div class="col-xs-3">
             <input type='button' class="btn btn-danger" value='-' onClick={this.remove_props.bind(this, i)}/>
         </div>
        </div> 
        </div>            
    )
 }


 set_run_job_values(i, event) {
    let values = [...this.state.values];
    values[i] =  event.target.value;
    this.setState({ values });
 }

 set_run_job_keys(i, event) {
  let keys = [...this.state.keys];
  keys[i] =  event.target.value;
  this.setState({ keys });
}
 
 add_props(){
   this.setState(prevState => ({ values: [...prevState.values, '']}))
 }
 
 remove_props(i){

    let values = [...this.state.values];
    values.splice(i,1); 
    this.setState({ values });

    let keys = [...this.state.keys];
    keys.splice(i,1);
    this.setState({ keys });
 }

 submit_run_job(job_name) {

   var final_props = []
   var i;
   for (i = 0; i < this.state.keys.length; i++) {
    var props = {["$key$"] : this.state.keys[i],["$value$"] : this.state.values[i]}
    final_props[i] =  props
   }

  var props = ''

  this.run_etl_job(this.state.job_name,final_props).then(({run_job}) => {
    this.set_notification(true)
    var data =  JSON.parse(JSON.stringify(run_job))
    if(data !== null){
        props = JSON.stringify(data["props"]) 
        var job_name = JSON.stringify(data["name"]) 
        this.set_job_deails(job_name,props)
   }});     
 }


  render(){
    var props = ''
    var notification_var = ""
    if(this.state.notification){
      if(this.state.is_error) {
         notification_var =
         <div class="alert alert-danger alert-dismissible fade show" role="alert">
            <h4 class="alert-heading">Error while submitting job!</h4>
            <p>{JSON.parse(JSON.stringify(this.state.errors)).map( (data) => data.message)}</p>
            <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
               <span aria-hidden="true">&times;</span>
            </button>
         </div>
       } else {
          notification_var =
          <div class="alert alert-success alert-dismissible fade show" role="alert">
             <h4 class="alert-heading">Job {this.state.job_name.replace(/^"(.*)"$/, '$1')} submitted successfully!  </h4>
             <p>{this.state.run_job_props}</p>
             <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
                <span aria-hidden="true">&times;</span>
             </button>
          </div>
        }  
    }
  
    return (
          <div class="container-fluid">
          <div>{notification_var}</div>
          <ul class = "pagination justify-content-center">
             <button class= "btn btn btn-sm" type="button " onClick = {() => {
                      this.props.get_etl_jobs() 
                }} >
               Refresh
             </button>
         </ul>
          <table id="joblist" class="table table-sm table-bordered">
            <thead class="thead-dark">
                  <tr>
                    <th>Active/Inactive</th>
                    <th>Job Name</th>
                    <th>Schedule</th>
                    <th>Next Schedule</th>
                    <th>Schedule Remaining Time</th>
                    <th>Job Last Run</th>
                    <th>Job Last Time</th>
                    <th>Action</th>
                  </tr>
                </thead>
                  <tbody>
                  {this.props.jobs.map( (data) => (
                    <tr>
                      <td>{(() => {
                           if (this.state.is_active && data.is_active) {
                             return (
                               <div className = "checkbox">
                                 <input type="checkbox"
                                     aria-describedby="UsernameHelp"
                                     data-toggle = "toggle"
                                     defaultChecked
                                     onClick={e => this.updateJobActiveState(data.name,false).then(({update_job_state}) => {
                                      this.props.get_etl_jobs()
                                      this.setState(prevState => ({  
                                        is_active: false
                                      }))
                                    })}>
                                 </input>
                               </div>
                             )
                           } else {
                            return (
                              <div className = "checkbox">
                                <input type="checkbox"
                                     aria-describedby="UsernameHelp"
                                     data-toggle = "toggle"
                                     onClick={e => this.updateJobActiveState(data.name,true).then(({update_job_state}) => {
                                      this.props.get_etl_jobs()
                                      this.setState(prevState => ({  
                                        is_active: true
                                      }))
                                    })}>
                                 </input>
                              </div>
                            )
                           }
                         })()}
                      </td>
                        <td> <button type="button" class="btn btn-link" data-dismiss="modal" onClick = {() => this.handleOnClick(data.name)}>{data.name}</button></td>
                      <td>{data.schedule}</td>
                      <td>{data.nextSchedule}</td>
                      <td data-order={data.nextSchedule}>{data.schduleRemainingTime}</td>
                      <td data-order={data.last_run_time}>
                      {(() => {

                           if (data.last_run_time === 0) {
                             return (
                               <div>
                               </div>
                             )
                           } else {
                             return (
                               <div>  
                                  {data.last_run_description}
                               </div>
                             )
                           } 
                         })()}
                      </td>
                      <td>{data.last_run_time}</td>

                      <td >
                      <div class='row'>
                      <div class='col-xs-12 button-wrapper' class="inner">
                          <button type="button" class="btn btn-secondary ml-2" data-toggle="modal" onClick = {() => this.show_modal(data.props)}>
                            Properties
                          </button>
                          <div class="modal fade" id="jobListeModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div class="modal-dialog" role="document">
                              <div class="modal-content">     
                                <div class="modal-header">
                                  <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                  </button>
                                </div>
                                <div class="modal-body">
                                </div>
                                <div class="modal-footer">
                                  <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                </div>
                              </div>
                            </div>
                          </div>


                          <div class="inner">
                             <button type="button" class="btn btn-warning ml-2" data-toggle="modal" data-target="#exampleModal" onClick = {() => 
                                      this.setState(prevState => ({
                                          job_name: data.name
                                  }))}>
                                 Run
                             </button>
                             <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                <div class="modal-dialog" role="document">
                                  <div class="modal-content">
                                    <div class="modal-header">
                                      <h5 class="modal-title" id="exampleModalLabel">Run Job</h5>
                                      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                      </button>
                                    </div>
                                    <div class="modal-body">
                                       {this.create_ui()}        
                                      <input type='button' class="btn btn-primary ml-2" value='Add Properties' onClick={this.add_props.bind(this)}/>
                                      <input type="button" class="btn btn-primary ml-2" value="Submit" onClick = {() => this.submit_run_job()}/>
                                    </div>
                                    <div class="modal-footer">
                                      <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                    </div>
                                  </div>
                                </div>
                              </div>
                           </div>
                           </div>
                           </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
            </table>
      </div>     
    );
  }
}
// export default JobList;
export default withRouter(JobList)