import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';


//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import LoaderComponent from './LoaderComponent';

class Credential extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { credential: [],
      loader:false,
      jdbc_values: [],
      aws_values: [],
      jdbc_keys: ["url","user","passsword","driver"],
      aws_keys: ["access_key","secret_key"],
      errors: [],
      is_error: false,
      credential_name:'',
      credential_type:'',
      insert_credential_type: ''
    }
    this.filterOperation = ["JDBC","AWS"]

  }

  async fetchEtlFlowCreds() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true
    }))

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
            credential {
              name
              type
              valid_from
            }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;    
  }

  async updateCreds(name,type,value) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    var props_updated = JSON.stringify(value).replaceAll("\"$","").replaceAll("$\"","")

    var query = ` mutation { update_credentials (name:\"${name}\", type:JDBC, value: ${props_updated}) { name } }`
    if(type === "aws") {
      query = ` mutation { update_credentials (name:\"${name}\", type:AWS, value: ${props_updated}) { name } }`
    }

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      )  
    })
    

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    const {data,errors} = await response.json();

    if(JSON.parse(JSON.stringify(data.update_credentials)) !== null){
      this.set_job_error_deails(false)
    }
    else {
      this.set_job_error_deails(true,JSON.parse(JSON.stringify(errors)))
    }

    return data;    
  }

  async insertCreds(name,type,value) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    var props_updated = JSON.stringify(value).replaceAll("\"$","").replaceAll("$\"","")

    var query = ` mutation { add_credentials (name:\"${name}\", type:JDBC, value: ${props_updated}) { name } }`
    if(type === "AWS") {
      query = ` mutation { add_credentials (name:\"${name}\", type:AWS, value: ${props_updated}) { name } }`
    }

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      )  
    })
    

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    const {data,errors} = await response.json();
    if(JSON.parse(JSON.stringify(data.add_credentials)) !== null){
      this.set_job_error_deails(false)
    }
    else {
      this.set_job_error_deails(true,JSON.parse(JSON.stringify(errors)))
    }

    return data;    
  }

  set_notification(state){
    this.setState(prevState => ({
      notification: state
    }))
  }


  create_update_cred_ui(){
    if(this.state.credential_type === "jdbc"){
    return this.state.jdbc_keys.map((el, i) => 
        <div key={i}>
        <div class="row">
          <div class="col-xs-4 col-sm-4 col-md-4">
            <div class="form-group">
               <input class="form-control" type="text" placeholder={this.state.jdbc_keys[i]} disabled/>
            </div>
          </div>  
          <div class="col-xs-7 col-sm-7 col-md-7">
            <div class="form-group">
              <input class="form-control"  type="text"  placeholder="***********" onChange={this.set_insert_values.bind(this, i)} />
            </div>    
          </div> 
        </div> 
        </div>            
    )
    }else if(this.state.credential_type === "aws"){
      return this.state.aws_keys.map((el, i) => 
        <div key={i}>
        <div class="row">
          <div class="col-xs-4 col-sm-4 col-md-4">
            <div class="form-group">
               <input class="form-control" type="text" placeholder={this.state.aws_keys[i]} disabled/>
            </div>
          </div>  
          <div class="col-xs-7 col-sm-7 col-md-7">
            <div class="form-group">
              <input class="form-control"  type="text"  placeholder="***********" onChange={this.set_insert_values.bind(this, i)} />
            </div>
          </div> 
        </div> 
        </div>            
    )
    }
 }

  create_add_cred_ui(){
    if(this.state.insert_credential_type === "JDBC"){
    return this.state.jdbc_keys.map((el, i) => 
        <div key={i}>
        <div class="row">
          <div class="col-xs-4 col-sm-4 col-md-4">
            <div class="form-group">
               <input class="form-control" type="text" placeholder={this.state.jdbc_keys[i]} disabled/>
            </div>
          </div>  
          <div class="col-xs-7 col-sm-7 col-md-7">
            <div class="form-group">
              <input class="form-control"  type="text"  placeholder="Type Value" onChange={this.set_values.bind(this, i)} />
            </div>
          </div> 
        </div> 
        </div>            
    )
    }else if(this.state.insert_credential_type === "AWS"){
      return this.state.aws_keys.map((el, i) => 
        <div key={i}>
        <div class="row">
          <div class="col-xs-4 col-sm-4 col-md-4">
            <div class="form-group">
               <input class="form-control" type="text" placeholder={this.state.aws_keys[i]} disabled/>
            </div>
          </div>  
          <div class="col-xs-7 col-sm-7 col-md-7">
            <div class="form-group">
              <input class="form-control"  type="text"  placeholder="Type Value" onChange={this.set_values.bind(this, i)} />
            </div>
          </div> 
        </div> 
        </div>            
    )
    }
 }

 set_add_cred_initial_state(){
  this.setState(prevState => ({  
    insert_credential_type: '',
    aws_values: [],
    jdbc_values: []
  }))
  document.getElementById("cred_name").value = ''
}

async set_update_cred_initial_state(){
  this.setState(prevState => ({  
    aws_values: [],
    jdbc_values: []
  }))
}

 set_values(i, event) {

  if(this.state.insert_credential_type === "JDBC") {
    let jdbc_values = [...this.state.jdbc_values];
    jdbc_values[i] =  event.target.value;
    this.setState({ jdbc_values });
   } else{ 
    let aws_values = [...this.state.aws_values];
    aws_values[i] =  event.target.value;
    this.setState({ aws_values });
   }
}
 
set_insert_values(i, event) {

  if(this.state.credential_type === "jdbc") {
    let jdbc_values = [...this.state.jdbc_values];
    jdbc_values[i] =  event.target.value;
    this.setState({ jdbc_values });
   } else{ 
    let aws_values = [...this.state.aws_values];
    aws_values[i] =  event.target.value;
    this.setState({ aws_values });
   }
}
 
 componentDidMount() {
    //calling getInfo api
    this.set_credentials()
  }


  set_credentials(){
    this.fetchEtlFlowCreds().then(({credential}) => {
      this.setState(prevState => ({
        credential: credential
      }))
    })
   }

  set_cred_update_deails(name){
    this.setState(prevState => ({
      credential_name: name
      }))
  } 

  update_credentials() {

    var final_props = []
    var i;

    if(this.state.credential_type === "jdbc") {
      for (i = 0; i < this.state.jdbc_keys.length; i++) {
        var props = {["$key$"] : this.state.jdbc_keys[i],["$value$"] : this.state.jdbc_values[i]}
       final_props[i] =  props
       }
     } else{ 
       for (i = 0; i < this.state.aws_keys.length; i++) {
         var props = {["$key$"] : this.state.aws_keys[i],["$value$"] : this.state.aws_values[i]}
        final_props[i] =  props
        }
     }
 
   var props = ''
 
   this.updateCreds(this.state.credential_name,this.state.credential_type,final_props).then(({update_credentials}) => {
     this.set_notification(true)
     var data =  JSON.parse(JSON.stringify(update_credentials))
   }) 

   this.setState(prevState => ({  
    credential_type: '',
    aws_values: [],
    jdbc_values: []
  }))
  }

  async add_credentials() {

    var final_props = []
    var i;

    if(this.state.insert_credential_type === "JDBC") {
     for (i = 0; i < this.state.jdbc_keys.length; i++) {
       var props = {["$key$"] : this.state.jdbc_keys[i],["$value$"] : this.state.jdbc_values[i]}
      final_props[i] =  props
      }
    } else{ 
      for (i = 0; i < this.state.aws_keys.length; i++) {
        var props = {["$key$"] : this.state.aws_keys[i],["$value$"] : this.state.aws_values[i]}
       final_props[i] =  props
       }
    }
 
   var props = ''
 
   var cred_name = document.getElementById("cred_name").value

   this.insertCreds(cred_name,this.state.insert_credential_type,final_props).then(({add_credentials}) => {
     this.set_notification(true)
     var data =  JSON.parse(JSON.stringify(add_credentials))
   }, () => this.set_add_cred_initial_state())

   this.setState(prevState => ({  
    insert_credential_type: '',
    aws_values: [],
    jdbc_values: []
  }))
  document.getElementById("cred_name").value = ''
  }

  set_job_error_deails(is_error,errors){
    this.setState(prevState => ({
      is_error: is_error,
      errors: errors                    
      }))
  }

  render(){

    var filter_button_name = ''
    if (this.state.credential_type !== '') {
      filter_button_name = this.state.credential_type  
    }else {
      filter_button_name = "Add Credential Type"
    } 

    var filter_insert_button_name = ''
    if (this.state.insert_credential_type !== '') {
      filter_insert_button_name = this.state.insert_credential_type  
    }else {
      filter_insert_button_name = "Add Credential Type"
    } 

    var filter_update_button_name = ''
    if (this.state.credential_type !== '') {
      filter_update_button_name = this.state.credential_type  
    }else {
      filter_update_button_name = "Add Credential Type"
    } 


    var notification_var = ""
    if(this.state.notification){
      if(this.state.is_error) {
         notification_var =
         <div class="alert alert-danger alert-dismissible fade show" role="alert">
            <h4 class="alert-heading">Error while Updatating Credential !</h4>
            <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
               <span aria-hidden="true">&times;</span>
            </button>
         </div>
       } else {
          notification_var =
          <div class="alert alert-success alert-dismissible fade show" role="alert">
             <h4 class="alert-heading">Credentials  submitted successfully!  </h4>
             <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
                <span aria-hidden="true">&times;</span>
             </button>
          </div>
        }  
    }

    if (this.state.loader){
      return (
        <div>
          <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
        </div>
      );
     }
    else{
      return (
        <div className="container">
        <div>{notification_var}</div>
          <ul class = "pagination justify-content-center">
            <button class= "btn btn btn-sm" type="button " onClick = {() => {
                   this.set_credentials()
                }} >
               Refresh
            </button>
            <div class="inner"> 
               <button type="button" class="btn btn-warning ml-2" data-toggle="modal" data-target="#exampleModal1">
                   Add New Credentials
               </button>
               <div class="modal fade" id="exampleModal1" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel1" aria-hidden="true">
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel1">Add Credentials</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </div>
                      <div class="modal-body">
                        <div class="row">
                          <div class="col-xs-7 col-sm-7 col-md-7">
                            <div class="form-group">
                               Credential Name
                               <input class="form-control" placeholder="Add Name"  type="text" id="cred_name"/>
                            </div>
                          </div> 
                          <div class="col-xs-7 col-sm-7 col-md-7">
                            <div class="form-group">
                            <div class="dropdown">
                              <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown">
                                {filter_insert_button_name}
                              </button>
                              <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                { this.filterOperation.map ( filter => (
                                  <a class="dropdown-item">
                                     <button class= "btn btn btn-sm" type="button " onClick = {() => {
                                     this.setState(prevState => ({
                                      insert_credential_type: filter
                                   }))
                                   }}> {filter} </button>
                                  </a>
                                ))}
                              </div>
                            </div> 
                            </div>
                          </div>  
                        </div> 
                        {this.create_add_cred_ui()} 
                        <input type="button" class="btn btn-primary ml-2" value="Submit" onClick = {() => this.add_credentials()}/>
                      </div>
                      <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                      </div>
                    </div>
                  </div>
                </div>
             </div>
          </ul>
          <table id="jobruns" class="table table-sm table-bordered">
              <thead class="thead-dark">
                    <tr>
                      <th>Credential Name</th>
                      <th>Type</th>
                      <th>Last Updated</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                    <tbody>
                    {this.state.credential.map( data => (
                      <tr>
                        <td>{data.name}</td>
                        <td>{data.type}</td>
                        <td>{data.valid_from}</td>
                        <td>
                        <div class="inner">
                             <button type="button" class="btn btn-warning ml-2" data-toggle="modal" data-target="#exampleModal"  onClick = {() => 
                                      this.setState(prevState => ({
                                        credential_name: data.name,
                                        credential_type: data.type
                                  }))}>
                                 Update
                             </button>
                             <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                <div class="modal-dialog" role="document">
                                  <div class="modal-content">
                                    <div class="modal-header">
                                      <h5 class="modal-title" id="exampleModalLabel">Update Credentials</h5>
                                      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                      </button>
                                    </div>
                                    <div class="modal-body">
                                    <div class="row">
                                        <div class="col-xs-7 col-sm-7 col-md-7">
                                          <div class="form-group">
                                             Credential Name
                                             <input class="form-control" placeholder={this.state.credential_name}  type="text" disabled/>
                                          </div>
                                        </div> 
                                        <div class="col-xs-7 col-sm-7 col-md-7">
                                          <div class="form-group">
                                              Type
                                             <input class="form-control" placeholder={this.state.credential_type}   type="text" disabled/>
                                          </div>
                                        </div>  
                                      </div> 
                                      {this.create_update_cred_ui()}        
                                      <input type="button" class="btn btn-primary ml-2" value="Submit" onClick = {() => this.update_credentials()}/>
                                    </div>
                                    <div class="modal-footer">
                                      <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                    </div>
                                  </div>
                                </div>
                              </div>
                           </div>
                        </td>
                      </tr>
                    ))}
                    </tbody>
               </table>  
             </div>
      );
   }      
  }
}
export default Credential;
