
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
 
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import ChartistGraph from 'react-chartist';
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
 
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
import Chartist from 'chartist/dist/chartist.min.js';
 
var ws_url = new URL("/ws/etlflow" , window.location.href)
ws_url.protocol = ws_url.protocol.replace("http","ws")
 
var ws  = new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
 
// new WebSocket('wss://devetlflow.startv.com/ws/etlflow/eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyIjoibWludF9hZG1pbiIsICJyb2xlIjoiYWRtaW4ifQ.gwiUv0vss-3gX5rkPOGTdJ_81n5N3W7Hv1yeGsJj5g8')
// new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
 
class MemoryInfo extends React.Component {
  constructor(props){
    super(props);
    const GRAPHQL_URL = '/ws/etlflow'
 
    this.state  = {
      messages:[],
      jobLogs:[],
      filter: '',
      limit: '',
      metrics: []
    }
 
    this.filterOperation = ['2','5','10']
    this.limitOperation = ['2','5','10','30','50']
 
    this.options = {
      low: 0,
      showArea: true,
      showPoint: false
    };
  }
 
  async call_api() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')
 
    var query =  ` query { jobLogs { job_name success failed }}`
   
    if(this.state.filter !== '' && this.state.limit !== '') {
      query =  ` query { jobLogs(limit: ${this.state.limit}, filter: ${this.state.filter}  ) { job_name success failed }}`
    } else  if (this.state.filter !== '') {
      query =  ` query { jobLogs(filter: ${this.state.filter}) { job_name success failed }}`
    } else  if (this.state.limit !== '') {
      query =  ` query { jobLogs(limit: ${this.state.limit}) { job_name success failed }}`
    }
 
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      ) 
    })
    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    }
    const {data} = await response.json()
    return data;   
  }
 
  async fetchEtlFlowMetrics() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')
 
    this.setState(prevState => ({
      loader: true
    }))
 
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
            metrics {
                active_jobs
                active_subscribers
                etl_jobs
                cron_jobs
                build_time
              }
           }
          `
        }
      ) 
    })
 
    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    }
    
    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))
 
    return data;   
  }
 

  call(close_ws){
 
    if(close_ws === "true"){
      ws.close(1000,"User closed the connection")
      document.getElementById("statslinknotify").innerText = "Real-Time Stats (Offline)"
      this.setState(prevState => ({
        messages:[]            
        }))
      console.log('ws closed')
    }
    else{
      document.getElementById("statslinknotify").innerText = "Real-Time Stats (Online)"
      ws  = new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
 
      ws.onopen = () => {
      console.log('ws connected')
      }  
  
      ws.onmessage = evt => {
      const message = evt.data
      var obj = JSON.parse(JSON.parse(evt.data)["data"]);
      var res = [obj];
      this.addMessage(res)
      } 
  
      ws.onclose = () => {
        console.log('disconnected')
      }
 
      var used_memory_data = {
       series: [[]]
        };
   
      var free_memory_data = {
        series: [[]]
      };
      
      var total_memory_data = {
        series: [[]]
      };
  
      var used_memory_chart = new Chartist.Line('.ct-chart', used_memory_data, this.options);
      var free_memory_chart = new Chartist.Line('.cpu-chart', free_memory_data, this.options);
      var total_memory_chart = new Chartist.Line('.cpu-chart-1', total_memory_data, this.options);
 
      
      this.updateUsedMemoryChart(used_memory_chart, used_memory_data, this.getUsedMemoryData(), 30)
      this.updateFreeMemoryChart(free_memory_chart, free_memory_data, this.getFreeMemoryData(), 30)
      this.updateTotalMemoryChart(total_memory_chart, total_memory_data, this.getTotalMemoryData(), 30)
    }
  }
 
  bar_chart(){
    new Chartist.Bar('.ct-chart-2', {
      labels: [0],
      series: [
        [0],
        [0],
      ]
    }, {
      seriesBarDistance: 10,
      axisX: {
        offset: 60
      },
      axisY: {
        offset: 80,
        labelInterpolationFnc: function(value) {
          return value
        },
        scaleMinSpace: 15
      }
    });       
  }
 
  bar_chart_data(){
    var job_name_labels = []
    var job_success = []
    var job_failed = []
 
    this.state.jobLogs.map( (data) => {
     job_name_labels.push(data.job_name)
     job_success.push(data.success)
     job_failed.push(data.failed)
    })
   
    new Chartist.Bar('.ct-chart-2', {
      labels: job_name_labels,
      series: [
        job_success,
        job_failed,
      ]
    }, {
      seriesBarDistance: 10,
      axisX: {
        offset: 60
      },
      axisY: {
        offset: 80,
        labelInterpolationFnc: function(value) {
          return value
        },
        scaleMinSpace: 15
      }
    });                   
  }
  componentDidMount() {
    this.call()
    this.call_api()
    this.set_jobLogs()
    this.set_metrics()
    this.bar_chart()  
    this.bar_chart_data() 
  }
 
  componentWillUnmount() {
    ws.close()
  }
 
  addMessage = message =>
    this.setState(prevState => ({
      messages: message
  }))
 
  getUsedMemoryData() {
    var seriesVals = [];
    this.state.messages.map( data => (
      seriesVals.push(data.Used_Memory)
    ))

    // console.log(JSON.stringify(seriesVals[0]))
    return seriesVals[0];
  }

  getFreeMemoryData() {
    var seriesVals = [];
    this.state.messages.map( data => (
      seriesVals.push(data.Free_Memory)
    ))

    // console.log(JSON.stringify(seriesVals[1]))
    return seriesVals[0];
  }

  getTotalMemoryData() {
    var seriesVals = [];
    this.state.messages.map( data => (
      seriesVals.push(data.Total_Memory)
    ))
    return seriesVals[0];
  }

  updateUsedMemoryChart(chart,data,point,length) {
    if(data.series[0].length >= length) {
      data.series[0].shift();
    }
    data.series[0].push(point);
    chart.update(data);
    setTimeout(() => { this.updateUsedMemoryChart(chart,data,this.getUsedMemoryData(),length) }, 1000);
  }
 
  updateFreeMemoryChart(chart,data,point,length) {
    if(data.series[0].length >= length) {
      data.series[0].shift();
    }
    data.series[0].push(point);
    chart.update(data);
    setTimeout(() => { this.updateFreeMemoryChart(chart,data,this.getFreeMemoryData(),length) }, 1000);
  }
 
  updateTotalMemoryChart(chart,data,point,length) {
    if(data.series[0].length >= length) {
      data.series[0].shift();
    }
    data.series[0].push(point);
    chart.update(data);
    setTimeout(() => { this.updateTotalMemoryChart(chart,data,this.getTotalMemoryData(),length) }, 1000);
  }
 

  async set_initial_state(){
    this.setState(prevState => ({ 
      filter: '',
      limit: ''
    }), () => this.set_jobLogs())
  }
 
  set_jobLogs(){
    this.call_api().then(({jobLogs}) => {
      this.setState(prevState => ({
        jobLogs: jobLogs
      }))
    })
   }

   set_metrics(){
    this.fetchEtlFlowMetrics().then(({metrics}) => {
      this.setState(prevState => ({
          metrics: metrics
      }))
    })
   }
 
  render(){
 

    var filter_button_name = ''
    var limit_button_name = ''
 
    if (this.state.filter !== '') {
      filter_button_name = this.state.filter
    }else {
      filter_button_name = "Add Filter (In Days)"
    }
 
    if (this.state.limit !== '') {
      limit_button_name = this.state.limit
    }else {
      limit_button_name = "Add limit"
    }
 
    return (
      <div class = "container-fluid">
      <div class="card">
      <div class="card-header">
        <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify" role = "button" 
          aria-expanded = "true" aria-controls = "streamnotify" id = "statslinknotify">
        </a>
        <ul class = "pagination float-right">
              <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                      this.call("false")
                }} >
                Connect
              </button>
              <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                  this.call("true")
                }} >
                Close
              </button>
        </ul>      
      </div>
    <div className = "collapse" id = "streamnotify">
    <div class= "card-body">
    <div class = "row">
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">Used Memory</div>
              <div class= "card-body">
                  <div class="ct-chart"></div>
              </div>
          </div>
      </div>
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">Free Memory</div>
              <div class= "card-body">
                  <div class="cpu-chart"></div>
              </div>
          </div>
      </div>
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">Total Memory</div>
              <div class= "card-body">
                  <div class="cpu-chart-1 "></div>
              </div>
          </div>
      </div>
      <div class="col-sm-3">
        <div class="card">
           <div class="card-header">Current Usage</div>
              <div class= "card-body">
                <div>
                   <ul class="list-group ">
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Used Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.messages.map( data => data.Used_Memory)}</span>
                       </li>
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Free Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.messages.map( data => data.Free_Memory)}</span>
                       </li>
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Total Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.messages.map( data => data.Total_Memory)}</span>
                       </li>
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Max Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.messages.map(  data => data.Max_Memory)}</span>
                       </li>
                   </ul>
                </div> 
              </div>
      </div>
      </div>
    </div>
    <br/>
    </div>
    </div>
    </div>
    <br/>
    <div class = "row">
    <div class="col-sm-9">
        <div class="card">
            <div class="card-header">
            <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify1" role = "button" 
              aria-expanded = "true" aria-controls = "streamnotify1">Etl Jobs Vs Count (Success/Failed)
            </a> 
              <ul class = "pagination float-right">
              <div class="dropdown">
                  <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown">
                    {filter_button_name}
                  </button>
                  <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    { this.filterOperation.map ( filter => (
                      <a class="dropdown-item">
                         <button class= "btn btn btn-sm" type="button " onClick = {() => {
                         this.setState(prevState => ({
                             filter: filter
                       }),() => this.set_jobLogs())}}> {filter} </button>
                      </a>
                    ))}
                  </div>
                </div>
                <div class="dropdown">
                  <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown">
                  {limit_button_name}
                  </button>
                  <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    { this.limitOperation.map ( limit => (
                      <a class="dropdown-item">
                         <button class= "btn btn btn-sm" type="button " onClick = {() => {
                         this.setState(prevState => ({
                             limit: limit
                       }),() => this.set_jobLogs())}}> {limit} </button>
                      </a>
                    ))}
                  </div>
                </div>
                <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                      this.set_initial_state()
                }}>Clear</button>
                <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                 
                     this.set_jobLogs() 
                
                     var job_name_labels = []
                     var job_success = []
                     var job_failed = []
 
                     this.state.jobLogs.map( (data) => {
                      job_name_labels.push(data.job_name)
                      job_success.push(data.success)
                      job_failed.push(data.failed)
                     })
   
                    new Chartist.Bar('.ct-chart-2', {
                      labels: job_name_labels,
                      series: [
                        job_success,
                        job_failed,
                      ]
                    }, {
                      seriesBarDistance: 10,
                      axisX: {
                        offset: 60
                      },
                      axisY: {
                        offset: 80,
                        labelInterpolationFnc: function(value) {
                          return value
                        },
                        scaleMinSpace: 15
                      }
                    });                   
                }} >
                Refresh
                </button>
              </ul>      
              </div>
              <div className = "collapse" id = "streamnotify1">
              <div class= "card-body">
                  <div class="ct-chart-2"></div>
              </div>
              </div>
        </div>
      </div>
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">
                <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify2" role = "button" 
                   aria-expanded = "true" aria-controls = "streamnotify" id = "statslinknotify">Metrics
                </a>
                <ul class = "pagination float-right">
                  <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                      this.fetchEtlFlowMetrics().then(({metrics}) => {
                        this.setState(prevState => ({
                            metrics: metrics
                        }))
                      })
                  }} >
                  Refresh
                  </button>
                </ul>      
              </div>
              <div className = "collapse" id = "streamnotify2">
              <div class= "card-body">
              <ul class="list-group ">
                    <li class="list-group-item d-flex justify-content-between align-items-center">
                        <b>Active Jobs</b>
                      <span class="badge badge-secondary badge-pill">{this.state.metrics.active_jobs}</span>
                    </li>
                    <li class="list-group-item d-flex justify-content-between align-items-center">
                        <b>Active Subscribers</b>
                      <span class="badge badge-secondary badge-pill">{this.state.metrics.active_subscribers}</span>
                    </li>
                    <li class="list-group-item d-flex justify-content-between align-items-center">
                        <b>Etl Jobs</b>
                      <span class="badge badge-secondary badge-pill">{this.state.metrics.etl_jobs}</span>
                    </li>
                    <li class="list-group-item d-flex justify-content-between align-items-center">
                        <b>Cron Jobs</b>
                      <span class="badge badge-secondary badge-pill">{this.state.metrics.cron_jobs}</span>
                    </li> 
                    <li class="list-group-item d-flex justify-content-between align-items-center">
                        <b>Build Time</b>
                      <span class="badge badge-secondary badge-pill">{this.state.metrics.build_time}</span>
                    </li>  
                 </ul>
              </div>
              </div>
          </div>
      </div>
   </div>
  </div>
  );
  }       
}
 
export default MemoryInfo;
 

